<template>
  <div class="ai-agents-page">
    <div class="ai-agents-page-wrap">
      <!-- Hero Section -->
      <section class="hero-section">
        <div class="hero-content">
          <h1 class="hero-title">AI Agents World</h1>
          <p class="hero-subtitle">Discover, create and share qunique AI agents</p>
          <div class="hero-avatars">
            <!-- Sample avatars with random floating animation -->
            <div v-for="avatar in heroAvatars" :key="avatar.id" class="hero-avatar" :style="avatar.style">
              <img :src="avatar.imageUrl" alt="" class="avatar">
            </div>
          </div>
          <div class="hero-actions">
            <router-link to="/ai-agent/create" class="btn btn-primary">
              <i class="fas fa-plus"></i> Create AI Agent
            </router-link>
            <router-link to="/about-bitcat-ai" class="btn btn-secondary">
              <i class="fas fa-info-circle"></i> About BITCAT AI
            </router-link>
          </div>
        </div>
      </section>

      <!-- Agents List Section -->
      <section class="agents-section">
        <div class="agents-header">
          <h2>AI Agents</h2>
          <div class="sort-controls">
            <select v-model="sortBy" @change="loadAgents" :disabled="currentTab === 'my'">
              <option value="newest">Newest First</option>
              <option value="oldest">Oldest First</option>
              <option value="popular">Most Popular</option>
            </select>
          </div>
        </div>

        <!-- all, my agents -->
        <div class="agents-tabs">
          <button 
            :class="['tab-btn', { active: currentTab === 'all' }]" 
            @click="switchTab('all')"
          >
            All
          </button>
          <button 
            :class="['tab-btn', { active: currentTab === 'my' }]" 
            @click="switchTab('my')"
          >
            My Agents
          </button>
        </div>

        <!-- Agents Grid -->
        <div class="agents-grid">
          <template v-if="displayedAgents.length > 0">
            <AiAgentCard
              v-for="agent in displayedAgents"
              :key="agent.agentTag"
              v-bind="agent"
              class="agents-agent-card"
            />
          </template>
          <div v-else class="empty-state">
            <div class="empty-state-content">
              <i class="fas fa-robot"></i>
              <h3>No AI Agents Found</h3>
              <p>Lets create an amazing AI agent!</p>
              <router-link to="/ai-agent/create" class="btn ">
                Create AI Agent
              </router-link>
            </div>
          </div>
        </div>

        <!-- Loading State -->
        <div v-if="loading" class="loading">
          <i class="fas fa-spinner fa-spin"></i> Loading agents...
        </div>

        <!-- Pagination -->
        <div class="pagination" v-if="totalPages > 1 && currentTab !== 'my'">
          <button 
            :disabled="currentPage === 1" 
            @click="changePage(currentPage - 1)"
            class="btn btn-sm"
          >
            <i class="fas fa-chevron-left"></i>
          </button>
          <span>Page {{ currentPage }} of {{ totalPages }}</span>
          <button 
            :disabled="currentPage === totalPages" 
            @click="changePage(currentPage + 1)"
            class="btn btn-sm"
          >
            <i class="fas fa-chevron-right"></i>
          </button>
        </div>
      </section>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import AiAgentCard from '@/components/ai/AiAgentCard.vue'

const router = useRouter()
const agents = ref([])
const loading = ref(false)
const currentPage = ref(1)
const totalPages = ref(0)
const totalAgents = ref(0)
const sortBy = ref('newest')
const currentTab = ref('all')
const myAgents = ref([])

const displayedAgents = computed(() => {
  return currentTab.value === 'all' ? agents.value : myAgents.value
})

const heroAvatars = computed(() => {
  const avatars = []
  for (let i = 0; i < 5; i++) {
    avatars.push({
      id: i,
      style: {
        left: `${10 + i * 20}%`,
        top: `${30 + Math.random() * 40}%`,
        '--random-x1': `${-150 + Math.random() * 300}px`,
        '--random-y1': `${-100 + Math.random() * 200}px`,
        '--random-x2': `${-150 + Math.random() * 300}px`,
        '--random-y2': `${-100 + Math.random() * 200}px`,
        '--random-x3': `${-150 + Math.random() * 300}px`,
        '--random-y3': `${-100 + Math.random() * 200}px`,
        '--random-duration': `${15 + Math.random() * 10}s`
      },
      imageUrl: `https://solanabitcat.com/data/ai-agents/bitcat-ai-agent-avatar-${Math.floor(Math.random() * 25) + 1}.png`
    })
  }
  return avatars
})

const loadAgents = async () => {
  loading.value = true
  try {
    const sort = {
      newest: 'DESC',
      oldest: 'ASC',
      popular: 'popular'
    }[sortBy.value]

    const response = await axios.get('/ai/agents', {
      params: {
        page: currentPage.value,
        limit: 20,
        sort
      }
    })

    agents.value = response.data.agents
    totalPages.value = response.data.totalPages
    totalAgents.value = response.data.totalAgents
  } catch (error) {
    console.error('Error loading agents:', error)
  } finally {
    loading.value = false
  }
}

const changePage = (page) => {
  currentPage.value = page
  loadAgents()
}

const switchTab = async (tab) => {
  currentTab.value = tab
  if (tab === 'my' && myAgents.value.length === 0) {
    loading.value = true
    try {
      const response = await axios.get('/ai/agents-by-user')
      myAgents.value = response.data
    } catch (error) {
      console.error('Error loading my agents:', error)
    } finally {
      loading.value = false
    }
  }
}

onMounted(() => {
  loadAgents()
})
</script>

